@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";
.Home-container {
  display: flex;
  flex-flow: row wrap;
}

.Home-container h1, h2, h3, h4, h5, h6 {
  text-transform: none;
}

.Home-container div {
  display: inline-block;
  flex-grow: 3;
}

.Home-container #front-text {
  padding: 10px; /* Some breathing room pls */
}

.Home-container #meeting-time {
  font-size: 150%;
}
.Home-container #slider {
    background-color: transparent;
    background: transparent;
}
.Home-container .image{
    width: 100px;
    height: 100px;
    border: none;
    overflow: hidden;
}
.slick-list {
    overflow: hidden;
    width: 100vw;
    margin: 0;
    padding: 0;
}
#front-images {
  background-image: url("https://images.unsplash.com/photo-1519882189396-71f93cb4714b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80");
  background-size: cover;
  flex-grow: 1;
}

#front-images p {
  background: rgba(255, 255, 255, 0.7);
}
